@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@300;500&display=swap);
.header {
  display: flex;
  align-items: center;
  height: 68px;
  margin: 0px 30px;
  background-color: transparent;
  transition: background-color 0.4s;
}

.header__title {
  color: #e40913;
  font-size: 25px;
  font-weight: 500;
  text-decoration: none;
  margin-right: 5px;
}

.primary-nav {
  list-style: none;
  display: flex;
  font-size: 11px;
  padding: 0px;
}

.primary-nav li {
  margin-left: 18px;
}

.text-link {
  color: inherit;
  text-decoration: inherit;
}

.secondary-nav {
  list-style: none;
  display: flex;
  font-size: 15px;
  position: absolute;
  padding: 0px;
  right: 4%;
}

.secondary-nav li {
  margin-right: 17px;
}

.about__container {
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  padding: 20px;
  border-radius: 5px;
  background-color: #141414;
  margin: 0 auto;
  margin-top: 100px;
  max-width: 25%;
  font-weight: 300;
}

.about__container span:first-child {
  font-size: 20px;
}

.about__container span:last-child {
  display: block;
  margin-top: 10px;
}

.movies .movie {
  width: 45%;
  margin-bottom: 70px;
  display: flex;
  align-items: flex-start;
  padding: 20px;
}

.movie img {
  position: relative;
  top: -50px;
  max-width: 150px;
  width: 100%;
  margin-right: 30px;
}

.movie__title,
.movie__year {
  margin: 0;
}

.movie__year {
  font-size: 13px;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Oswald", sans-serif;
  font-weight: 300;
  background-color: #141414;
  color: white;
}

html,
body,
#root,
.container {
  height: 100%;
}

.loader {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
}

.loader__text {
  position: relative;
  top: -70px;
}

.movies {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 50px 50px;
}

